function Bannerr(props) {
  if (props.newProp) console.log(props.newProp);
  const contentPosition = props.contentPosition
    ? props.contentPosition
    : "left";
  const totalItems = props.length ? props.length : 1;
  const mediaLength = totalItems;

  let items = [];

  for (let i = 0; i < mediaLength; i++) {
    const item = props.item.Items[i];

    const media = (
      <Grid item xs={12 / totalItems} spacing={4} key={item.Name}>
        <CardMedia
          className="Mediaa"
          image={item.Image}
          title={item.Name}
        ></CardMedia>
      </Grid>
    );
    items.push(media);
  }

  return (
    <Card raised className="Bannerr">
      <Grid container spacing={4} className="BannerrGrid mt-3 p-4">
        {items}
      </Grid>
    </Card>
  );
}

