// import Image from "next/image";
import React from "react";
import { Button } from "@material-ui/core";
import { useHistory } from "react-router-dom";
// import Link from "react-router-dom";
export default function Room({ room }) {
  const history = useHistory();
  const {
    name,
    // slug,
    // images,
    // unit,
    // price,
    // address,
    // onWards,
    // Beds,
    // Baths,
    // sqft,
    tags,
  } = room;

  return (
    <div className="col-md-4 col-12 mx-auto p-2">
      <div className="card shadow-lg border-0 ">
        <div className="room">
          <img
            style={{ height: "250px" }}
            src={room?.images}
            alt="single room"
            className="img-fluid"
          />
          <div className="price-top">
            <h6 style={{ color: "white" }}>
              Rs {room?.price}
              {room?.unit} {room?.onWards && "OnWards*"}
            </h6>
          </div>

          <p className="room-info">{name}</p>
          <div className="px-3">
            <p>{room?.address}</p>
            <p>
              {room.Beds && ` Beds:${room.Beds}`}
              {room.Baths && ` Baths:${room.Baths}`}
              {room.sqft && ` sqft:${room.sqft}`}
            </p>
            {/* <p>
              Beds:{Beds},Baths:{Baths},sqft:{sqft}
            </p> */}
            <p> {room.tags.toString().slice(0, 30)}...</p>
          </div>
          <div className="text-center">
            {" "}
            <Button
              variant="contained"
              color="primary"
              className="mb-3 "
              onClick={() => history.push(`/property/${room._id}`)}
            >
              View more
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}
