import {
  Button,
  Card,
  CardContent,
  CardMedia,
  Grid,
  Typography,
} from "@material-ui/core";
import autoBind from "auto-bind";
import React from "react";
import Carousel from "react-material-ui-carousel";
import "./Crousal1.css";
import { useHistory } from "react-router-dom";

function Bannerr(props) {
  const history = useHistory();

  // if (props.newProp)
  // const contentPosition = props.contentPosition
  //   ? props.contentPosition
  //   : "left";
  const totalItems = props.length ? props.length : 1;
  // const mediaLength = totalItems;
  let items = [];
  // for (let i = 0; i < mediaLength; i++) {
  //   const item = props.item.Items[i];
  const media = (
    <Grid
      item
      xs={12 / totalItems}
      spacing={4}
      onClick={() => history.push(`/property/${props?.item?._id}`)}
    >
      <CardMedia className="Mediaa" image={props?.item?.images}>
        <div className="price-top">
          <h6 style={{ color: "white" }}>
            Rs {props?.item?.price}
            {props?.item?.unit} {props?.item?.onWards && "OnWards*"}
          </h6>
        </div>
      </CardMedia>
    </Grid>
  );
  items.push(media);
  // }
  return (
    <Card raised className="Bannerr">
      <Grid container spacing={4} className="BannerrGrid mt-3 p-4">
        {items}
      </Grid>
    </Card>
  );
}

class BannerrExample extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      autoPlay: true,
      animation: "fade",
      indicators: true,
      timeout: 500,
      navButtonsAlwaysVisible: true,
      navButtonsAlwaysInvisible: false,
      cycleNavigation: true,
      number: 0,
    };
    autoBind(this);
  }

  toggleAutoPlay() {
    this.setState({
      autoPlay: !this.state.autoPlay,
    });
  }

  toggleIndicators() {
    this.setState({
      indicators: !this.state.indicators,
    });
  }

  toggleNavButtonsAlwaysVisible() {
    this.setState({
      navButtonsAlwaysVisible: !this.state.navButtonsAlwaysVisible,
    });
  }

  toggleNavButtonsAlwaysInvisible() {
    this.setState({
      navButtonsAlwaysInvisible: !this.state.navButtonsAlwaysInvisible,
    });
  }

  toggleCycleNavigation() {
    this.setState({
      cycleNavigation: !this.state.cycleNavigation,
    });
  }

  changeAnimation(event) {
    this.setState({
      animation: event.target.value,
    });
  }

  changeTimeout(event, value) {
    this.setState({
      timeout: value,
    });
  }

  render() {
    return (
      <div style={{ color: "#494949" }}>
        <h2>Latest Properties</h2>

        <Carousel
          className="Example"
          autoPlay={this.state.autoPlay}
          animation={this.state.animation}
          indicators={this.state.indicators}
          timeout={this.state.timeout}
          cycleNavigation={this.state.cycleNavigation}
          navButtonsAlwaysVisible={this.state.navButtonsAlwaysVisible}
          navButtonsAlwaysInvisible={this.state.navButtonsAlwaysInvisible}
          onChange={(now, previous) =>
            this.setState({
              number: now,
            })
          }
          // fullHeightHover={false}
          // navButtonsProps={{style: {backgroundColor: 'cornflowerblue', borderRadius: 0}}}
          // navButtonsWrapperProps={{ style: { bottom: "0", top: "unset" } }}
          // indicatorContainerProps={{style: {margin: "20px"}}}
          // NextIcon='next'
        >
          {this.props.rooms?.rootUser?.slice(0, 6)?.map((item, index) => {
            return (
              <Bannerr
                search={this.props.search}
                setsearch={this.props.setsearch}
                searching={this.props.searching}
                setsearching={this.props.setsearching}
                item={item}
                key={index}
                contentPosition={item.contentPosition}
              />
            );
          })}
        </Carousel>
      </div>
    );
  }
}

export default BannerrExample;
