import { Button, Fab } from "@material-ui/core";
import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";
import PhoneIcon from "@material-ui/icons/Phone";
// import vid from "https://firebasestorage.googleapis.com/v0/b/blackstone-5dc37.appspot.com/o/video%2FBreakfast%20-%2025639.mp4?alt=media&token=8e64faef-b604-4a23-9666-12f382b92a6d";
import WhatsAppIcon from "@material-ui/icons/WhatsApp";
import React from "react";
import Foot from "./Component/Footer";
import Loader from "./Component/loader";
import CustomModal from "./Component/Modal/Modal";
import Room from "./Component/Room";
import Room2 from "./Component/Room2";
import Crousal from "./Component/Crousal";
import SecondaryCrousal from "./Component/SecondaryCrousal";
import PropertyCrousal from "./Component/PropertyCrousal";
import MainPropertyCrousal from "./Component/MainPropertyCrousal";
import SmallCrousal from "./Component/SmallCrousal";
import SmallCrousal1 from "./Component/SmallCrousal1";

function Home() {
  const [search, setsearch] = React.useState("");
  const [searching, setsearching] = React.useState("");
  const [advsearch, setadvsearch] = React.useState("");
  const [advsearching, setadvsearching] = React.useState("");
  const [location, setlocation] = React.useState("");
  const [sqft, setsqft] = React.useState("");
  const [searchlocation, setsearchlocation] = React.useState("");
  const [searchsqft, setsearchsqft] = React.useState("");

  const [rooms, setrooms] = React.useState([]);
  const [limit, setlimit] = React.useState(6);
  const [modal, setModal] = React.useState(false);
  const [loader, setloader] = React.useState(false);

  React.useEffect(() => {
    if (searching.length > 0) {
      window.scrollTo(0, 640);
      // document.body.scrollTop = 0;
    }
  }, [searching, location]);
  React.useEffect(() => {
    if (
      advsearching.length > 0 ||
      searchsqft.length > 0 ||
      searchlocation.length > 0
    ) {
      window.scrollTo(0, 1800);
    }
  }, [advsearching, searchsqft, searchlocation]);
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  React.useEffect(() => {
    setTimeout(() => {
      setModal(true);
    }, 10000);
  }, []);
  const Property = async (e) => {
    if (searching.length === 0) {
      try {
        setloader(true);
        const res = await fetch(
          "api/advGetProperty",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({
              advsearching,
              searchsqft,
              searchlocation,
              limit,
            }),
          }
        );
        const data = await res.json();
        if (!res.status === 200) {
          const error = new Error("invaldated..........");
          throw error;
        }
        setrooms(data);
        setloader(false);
      } catch (err) {
        setloader(false);
      }
    } else {
      try {
        setloader(true);
        const res = await fetch(
          "api/GetProperty",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({ searching, limit }),
          }
        );
        const data = await res.json();
        if (!res.status === 200) {
          const error = new Error("invaldated..........");
          throw error;
        }
        setrooms(data);
        setloader(false);
      } catch (err) {
        setloader(false);
      }
    }
  };

  React.useEffect(() => {
    if (searching.length > 0) {
      Property();
      if (
        advsearching.length > 0 ||
        searchsqft.length > 0 ||
        searchlocation.length > 0
      ) {
        setadvsearching(" ");
        setsearchsqft(" ");
        setsearchlocation(" ");
      }
    } else if (
      advsearching.length > 0 ||
      searchsqft.length > 0 ||
      searchlocation.length > 0
    ) {
      Property();

      if (searching.length > 0) {
        setsearching("");
      }
    } else {
      Property();
    }
  }, [searching, limit, advsearching, searchsqft, searchlocation]);

  return (
    <div>
      <div className="d-none d-md-block pt-3">
        <Crousal
          search={search}
          setsearch={setsearch}
          searching={searching}
          setsearching={setsearching}
        />
        <div className="row Search__Bar py-3 ">
          <div className="col-2"></div>
          <div className="col-3">
            <input
              type="text"
              class="form-control "
              placeholder="Search for property name,Beds,Baths etc."
              aria-label="Recipient's username"
              aria-describedby="button-addon2"
              onChange={(e) => setadvsearch(e.target.value)}
              // onKeyPress={(e) => {
              //   if (e.key === "Enter") {
              //     setadvsearching(advsearch);
              //   }
              // }}
            />
          </div>
          <div className="col-3">
            <input
              type="text"
              class="form-control "
              placeholder="Location"
              aria-label="Recipient's username"
              aria-describedby="button-addon2"
              onChange={(e) => setlocation(e.target.value)}
              // onKeyPress={(e) => {
              //   if (e.key === "Enter") {
              //     setsearchlocation(location);
              //   }
              // }}
            />
          </div>
          <div className="col-1">
            <input
              type="text"
              class="form-control "
              placeholder="Sqft"
              aria-label="Recipient's username"
              aria-describedby="button-addon2"
              onChange={(e) => setsqft(e.target.value)}
              // onKeyPress={(e) => {
              //   if (e.key === "Enter") {
              //     setsearchsqft(sqft);
              //   }
              // }}
            />
          </div>
          <div className="col-1">
            <Button
              color="primary"
              variant="contained"
              onClick={() => {
                setadvsearching(advsearch);
                setsearchlocation(location);
                setsearchsqft(sqft);
                setsearching("");
              }}
            >
              Search
            </Button>
          </div>
          <div className="col-2"></div>
        </div>
      </div>

      <div className="heroimage">
        <CustomModal modal={modal} setModal={setModal} />
        <div className="Homevideo2">
          <SecondaryCrousal />
        </div>
        {/* <div className={styles.dicard}></div> */}

        <div className="herotext">
          <h1>Welcome to blackstone</h1>
          <p className="d-none d-sm-block ">
            Discover your dream home in the millennium city Gurugram
          </p>
          <div class="row mb-3">
            <input
              type="text"
              class="form-control"
              placeholder="Search for Property"
              aria-label="Recipient's username"
              aria-describedby="button-addon2"
              onChange={(e) => setsearch(e.target.value)}
            />

            <Button
              color="primary"
              variant="contained"
              onClick={() => setsearching(search)}
            >
              Search
            </Button>
          </div>
        </div>
        <div className="FixedButton">
          <div className="py-2">
            <a href="https://api.whatsapp.com/send?phone=918810590661">
              <Fab color="primary" aria-label="add">
                <WhatsAppIcon />
              </Fab>
            </a>
          </div>
          <div className="py-2">
            <a href="https://www.facebook.com/">
              <Fab color="primary" aria-label="add">
                <FacebookIcon />
              </Fab>
            </a>
          </div>
          <div className="py-2">
            <a href="https://www.instagram.com/accounts/login/">
              <Fab color="primary" aria-label="add">
                <InstagramIcon />
              </Fab>
            </a>
          </div>
          <div className="py-2">
            <a href="tel:+91-8810590661">
              <Fab color="primary" aria-label="add">
                <PhoneIcon />
              </Fab>
            </a>
          </div>
        </div>
      </div>
      {searching.length === 0 ? <Room2 /> : null}
      <div className="container">
        <div className="Crousal1 py-5">
          <MainPropertyCrousal rooms={rooms} />
        </div>
        <div className="Crousal2 py-5">
          <PropertyCrousal rooms={rooms} />
        </div>
      </div>
      <div className="container">
        <div className="row my-5">
          <h3 className="text-center py-3 dicoverText">
            Discover Our Best Property in Gurgaon
          </h3>
          {/* {rooms?.rootUser?.map((item) => {
            return <Room key={item.id} room={item} />;
          })} */}
          {rooms.rootUser?.length > 0 ? (
            rooms.rootUser.map((item) => {
              return <Room key={item.id} room={item} />;
            })
          ) : (
            <div style={{ marginTop: "120px", marginBottom: "120px" }}>
              <h2 className="text-center">Sorry no data Found!</h2>
            </div>
          )}
          {rooms.rootUser?.length > 0 && (
            <div className="d-flex justify-content-center pt-5">
              <Button
                variant="outlined"
                color="primary"
                onClick={() => {
                  setlimit(limit + 3);
                }}
              >
                Load More
              </Button>
            </div>
          )}
        </div>
      </div>
      <div className="container">
        <div className="Crousal1 py-5">
          <SmallCrousal />
        </div>
        <div className="Crousal2 py-5">
          <SmallCrousal1 />
        </div>
      </div>
      <Foot />
      <Loader val={loader} />
    </div>
  );
}

export default Home;
