import React, { useState } from "react";
import logo from "./undraw_Login_re_4vu2.svg";
import { useHistory } from "react-router";
import "./Login.css";
import { Link } from "react-router-dom";
import { useCookies } from "react-cookie";
import Footer from "./Component/Footer";
import Loader from "./Component/loader";
import { FormatColorReset } from "@material-ui/icons";
function Login() {
  const [cookies, setCookie] = useCookies(["cookie"]);
  const [loader, setloader] = useState(false);
  const [Login, setLogin] = useState({
    email: "",
    password: "",
  });
  const history = useHistory();
  const ChangeHandler = (e) => {
    var name = e.target.name;
    var value = e.target.value;
    setLogin({ ...Login, [name]: value });
  };
  const handelSubmit = async (e) => {
    setloader(true);
    e.preventDefault();
    const { email, password } = Login;
    const res = await fetch("api/login", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ email, password }),
    });
    const data = await res.json();
    if (res.status === 400 || !data) {
      window.alert("invalid");
      setloader(false);
    } else {
      window.alert("success fulll");
      setloader(false);
      setCookie("cookie", "qwertyasdfgzxcvb1237654");

      history.push("/Admin");
    }
  };
  return (
    <div>
      <div className="container pt-5 mt-5">
        <div className="row">
          <div className="col-10 mx-auto mt-5 main_div">
            <div className="row">
              <div className="col-6 ">
                <h2 className="text-center mt-4 mb-5">Login</h2>
                <div className="row">
                  <div className="col-8 m-auto">
                    <form method="POST" onSubmit={handelSubmit}>
                      <div className="mb-3">
                        <input
                          type="email"
                          name="email"
                          onChange={ChangeHandler}
                          className="form-control"
                          placeholder="Your Email..."
                        />
                      </div>

                      <div className="mb-3">
                        <input
                          name="password"
                          onChange={ChangeHandler}
                          type="password"
                          className="form-control"
                          placeholder="password..."
                        />
                      </div>
                      <Link to="/register" className="m-4">
                        create account
                      </Link>

                      <button type="submit" className="btn btn-primary my-4">
                        Submit
                      </button>
                    </form>
                  </div>
                </div>
              </div>
              <div className="col-6 m-auto">
                <img className="img" src={logo} alt="asd" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div style={{ marginTop: "280px" }}>
        <Footer />
        <Loader val={loader} />
      </div>
    </div>
  );
}

export default Login;
