import { Paper, Button } from "@material-ui/core";
import React from "react";
import { useHistory, useParams } from "react-router-dom";
import ViewProperties from "../Component/Modal/ViewProperties";
function Propertiess() {
  const [Properties, setProperties] = React.useState(null);
  const [modal, setModal] = React.useState(false);
  const [PdfId, setPdfId] = React.useState(null);

  let { id } = useParams();
  const history = useHistory();
  React.useEffect(() => {
    const CallProperties = async (id) => {
      const res = await fetch(
        "api/PropertiesSingle",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ id }),
        }
      );
      const data = await res.json();
      if (data.rootUser.length > 0) {
        setProperties(data.rootUser);
        console.log(data);
      } else {
        window.alert("please login first");
        history.push("/login");
      }
    };
    CallProperties(id);
  }, []);
  return (
    <div className="container pt-5 mt-5">
      <div className="row">
        <ViewProperties modal={modal} setModal={setModal} PdfId={PdfId} />

        <div className="col-sm-12 col-md-6 m-auto  ">
          {Properties && (
            <Paper elevation={5} className="p-4">
              <img
                src={Properties[0]?.images}
                style={{ width: "100%", height: "300px" }}
                alt="image"
              />

              <h2 className="text-center py-2">{Properties[0]?.name}</h2>
              <p>
                <strong>price:-</strong>
                {Properties[0]?.price} {Properties[0]?.unit}*
                {Properties[0]?.onWards && "Onwards"}
              </p>
              <p>
                <strong>Address:-</strong>
                {Properties[0]?.address}
              </p>
              <p>
                <strong>Beds:-</strong>
                {Properties[0]?.Beds}
              </p>
              <p>
                <strong>Baths:-</strong>
                {Properties[0]?.Baths}
              </p>
              <p>
                <strong>sqft:-</strong>
                {Properties[0]?.sqft}
              </p>
              <p>
                <strong>tags:-</strong>
                {Properties[0]?.tags.toString()}
              </p>

              <embed
                className="d-none d-md-block py-3"
                type="application/pdf"
                src={Properties[0]?.pdfs + "#toolbar=0"}
                width="100%"
                height="400px"
              />
              {/* <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  setModal(true);
                  setPdfId(Properties[0]?.pdfs);
                }}
              >
                View Brochure
              </Button> */}
              <a href={Properties[0]?.pdfs} className="py-3">
                <Button variant="contained" color="primary">
                  Download Brochure
                </Button>
              </a>
            </Paper>
          )}
        </div>
      </div>
    </div>
  );
}

export default Propertiess;
