import { Button, TextField } from "@material-ui/core";
import Modal from "@material-ui/core/Modal";
import { makeStyles } from "@material-ui/core/styles";
import React from "react";
import logo from "../../Assests/blackstone_logo.png";

function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50 + rand();
  const left = 50 + rand();

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function SimpleModal({ modal, setModal }) {
  const classes = useStyles();
  const [Data, setData] = React.useState({
    name: "",
    number: "",
    email: "",
  });
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = React.useState(getModalStyle);

  const handleClose = () => {
    setModal(false);
  };
  const changeHandler = (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setData({ ...Data, [name]: value });
  };
  const handelSubmit = async (e) => {
    e.preventDefault();
    const { name, number, email } = Data;
    const res = await fetch("api/PostEnquiry", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name,
        number,
        email,
      }),
    });
    const data = await res.json();

    alert(data.message);
    setModal(false);
  };
  const body = (
    <div style={modalStyle} className={classes.paper}>
      <h4 className="text-center">Enquiry Now</h4>
      <div className="row">
        <div className="col-6">
          <img
            src={logo}
            className="text-center img-fluid pt-4"
            style={{ maxwidth: "300px", maxheight: "300px", marginTop: "20px" }}
            alt="customer4"
          />
        </div>
        <div className="col-6">
          <form className="m-auto">
            <TextField
              id="standard-basic"
              label="Name"
              name="name"
              onChange={changeHandler}
            />
            <TextField
              id="standard-basic"
              label="Mobile No."
              name="number"
              onChange={changeHandler}
            />
            <TextField
              id="standard-basic"
              label="Email"
              name="email"
              onChange={changeHandler}
            />
            <div className="d-flex justify-content-center p-3">
              <Button
                variant="contained"
                color="primary"
                onClick={handelSubmit}
              >
                Submit
              </Button>
            </div>
          </form>
        </div>
      </div>
      <SimpleModal />
    </div>
  );

  return (
    <div>
      <Modal
        open={modal}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </div>
  );
}
