import React from "react";
import { Divider, Paper } from "@material-ui/core";
import { Button } from "@material-ui/core";
import parse from "html-react-parser";
import ShowMoreText from "react-show-more-text";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import Foot from "./Component/Footer";
import Loader from "./Component/loader";
import { Link } from "react-router-dom";
function Blogs(props) {
  const [RecentData, setRecentData] = React.useState([]);
  const [Data, setData] = React.useState([]);
  const [limit, setlimit] = React.useState(3);
  const [searching, setsearching] = React.useState("");
  const [sear, setsear] = React.useState("");
  const [loader, setloader] = React.useState(false);
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const executeOnClick = (isExpanded) => {
    console.log(isExpanded);
  };
  const RecentPost = async () => {
    try {
      setloader(true);
      const res = await fetch("api/Blogspage", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      });
      const data = await res.json();
      if (!res.status === 200) {
        const error = new Error("invaldated..........");
        throw error;
      }
      setRecentData(data);
      setloader(false);
      //   setval(true);
    } catch (err) {
      setloader(false);
    }
  };
  const MainBlog = async (e) => {
    try {
      setloader(true);
      const res = await fetch("api/allblogs", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ limit, searching }),
      });
      const data = await res.json();
      if (!res.status === 200) {
        const error = new Error("invaldated..........");
        throw error;
      }
      setData(data);
      setloader(false);
    } catch (err) {
      setloader(false);
    }
  };
  React.useEffect(() => {
    RecentPost();
    MainBlog();
  }, [limit, searching]);
  const handelSubmit = (e) => {
    e.preventDefault();
    setsearching(sear);
  };
  return (
    <div className="pt-5">
      <div className="container py-5">
        <h3 className="py-3">Blogs</h3>
        <div className="row ">
          <div className=" col-sm-12 col-md-4 ">
            <Paper elevation={4} className="my-2">
              <div className="container p-3">
                <p>Blog Search</p>
                <form onSubmit={handelSubmit}>
                  <input onChange={(e) => setsear(e.target.value)} />
                  <Button type="submit">Search</Button>
                </form>
              </div>
            </Paper>
            <Paper elevation={4} className="my-2 d-none d-md-block">
              <div className="container p-3">
                <p>Recent Posts</p>
                {RecentData?.rootUser?.map((res, id) => (
                  <div className="row" key={id}>
                    <div className="col-2 d-flex justify-content-center align-items-center">
                      <ArrowRightIcon />
                    </div>
                    <div className="col-10">
                      <Link
                        to={`/blog/${res._id}`}
                        style={{ textDecoration: "none" }}
                      >
                        <p className="Bold">{res.name}</p>
                      </Link>
                    </div>
                  </div>
                ))}
              </div>
            </Paper>
          </div>
          <div className=" col-sm-12 col-md-8">
            {Data?.rootUser?.map((res, id) => (
              <div className="col-md-12 col-12 mx-auto p-2" key={id}>
                <div className="card shadow-lg border-0 ">
                  <div className="room">
                    <img
                      src={res.images}
                      // layout="responsive"
                      style={{ maxHeight: "400px" }}
                      alt="single room"
                      className="img-fluid"
                    />

                    <p className="room-info">{res.name}</p>
                    <div className="px-3 room-Data py-4">
                      {" "}
                      <ShowMoreText
                        /* Default options */
                        lines={3}
                        more="Show more"
                        less="Show less"
                        className="content-css"
                        anchorClass="my-anchor-css-class"
                        onClick={executeOnClick}
                        expanded={false}
                        width={280}
                      >
                        {parse(`${res.description}`)}
                      </ShowMoreText>
                    </div>
                    <Divider />
                    <div className="row py-3">
                      <div className="col-md-6 d-flex justify-content-center ">
                        <p>By BlackStone</p>
                      </div>
                      <div className="col-md-6 text-center ">
                        <p>Created At:-{res?.created_at?.slice(0, 10)}</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
            <div className="d-flex justify-content-center pt-5">
              <Button
                variant="outlined"
                color="primary"
                onClick={() => {
                  setlimit(limit + 2);
                }}
              >
                Load More
              </Button>
            </div>
          </div>
        </div>
      </div>
      <Foot />
      <Loader val={loader} />
    </div>
  );
}
export default Blogs;
