import React, { useEffect, useState } from "react";
import "./Contact.css";
import locationimg from "./Assests/location.PNG";
import { Paper } from "@material-ui/core";
import Footer from "./Component/Footer";
import smalllocationimg from "./Assests/smallmap1.PNG";
function Contact() {
  const [mainData, setmainData] = useState({
    name: "",
    email: "",
    phone: "",
    message: "",
  });
  console.log(mainData);
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const userContact = async () => {
    try {
      const res = await fetch("api/getData", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      });
      const data = await res.json();

      console.log(data);
      setmainData({
        ...mainData,
        name: data.name,
        email: data.email,
        phone: data.phone,
      });

      if (!res.status === 200) {
        const error = new Error("invaldated..........");
        throw error;
      }
    } catch (err) {
      console.log(err);
    }
  };
  useEffect(() => {
    userContact();
  }, []);
  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setmainData({ ...mainData, [name]: value });
  };
  const handelSubmit = async (e) => {
    e.preventDefault();
    const { name, email, phone, message } = mainData;
    const res = await fetch("api/contact", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name,
        email,
        phone,
        message,
      }),
    });
    const data = await res.json();
    if (!data) {
      console.log("message");
    } else {
      alert("message send");
      setmainData({ ...mainData, message: "" });
    }
  };
  return (
    <div className="main_block pt-4">
      <div className="container pt-5">
        <div className="main__down my-5 pb-5 shadow rounded">
          <h2 className="text-center py-5">Get In Touch</h2>
          <div className="main__down__div  px-md-5">
            <form method="POST" onSubmit={handelSubmit}>
              <div class="row">
                <div class="col-md-4 py-2 py-md-0 col-sm-12 ">
                  <input
                    type="text"
                    name="name"
                    value={mainData.name}
                    class="form-control"
                    onChange={handleChange}
                    placeholder="Your Name"
                  />
                </div>
                <div class="col-md-4 py-2 py-md-0 col-sm-12 ">
                  <input
                    name="email"
                    type="text"
                    value={mainData.email}
                    class="form-control"
                    onChange={handleChange}
                    placeholder="Your Email"
                  />
                </div>
                <div class="col-md-4 py-2 py-md-0 col-sm-12 ">
                  <input
                    type="text"
                    name="phone"
                    value={mainData.phone}
                    class="form-control"
                    onChange={handleChange}
                    placeholder="Your PhoneNumber"
                  />
                </div>
              </div>
              <div className="row px-3 py-2 py-md-5 pb-5">
                <textarea
                  rows="5"
                  name="message"
                  onChange={handleChange}
                  value={mainData.message}
                />
              </div>
              <button type="submit" className="btn btn-primary mb-4">
                Submit
              </button>
            </form>
          </div>
        </div>
        <div className="main__up">
          <div className="row ">
            <div className="col-sm-12 col-md-4 ">
              <div className="box shadow Rounded">
                <h3>Phone</h3>
                <p>
                  8810590661 <br /> 7428525727
                </p>
              </div>
            </div>
            <div className="col-sm-12 col-md-4 ">
              <div className="box shadow Rounded">
                <h3>Email</h3>
                <p className="pb-4">
                  info@blackstoneconsultant.com <br />
                </p>
              </div>
            </div>

            <div className="col-sm-12 col-md-4 ">
              <div className="box shadow Rounded">
                <h3>Address</h3>
                <p>
                  BlackStone consultant,JMD Megapolis,Sohna Rd, Sector 48,
                  Gurugram, Haryana 122018
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="container">
        <div className="row py-5">
          <div
            className="col-12  Rounded 
          m-auto
          "
          >
            <Paper elevation={5}>
              <img
                className="d-none d-md-block"
                src={locationimg}
                alt="location"
                width="100%"
                height="500px"
              />
              <img
                className="d-md-none"
                src={smalllocationimg}
                alt="location"
                width="100%"
                height="500px"
              />{" "}
            </Paper>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default Contact;
