import {
  Button,
  Divider,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Paper,
  //   Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
} from "@material-ui/core";
import { useCookies } from "react-cookie";

// import AddIcon from "@material-ui/icons/Add";
import DeleteIcon from "@material-ui/icons/Delete";
// import { useRouter } from "next/router";
import VisibilityIcon from "@material-ui/icons/Visibility";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import FloatingIcons from "./Component/Floating";
import Loader from "./Component/loader";
import AddBlogsModal from "./Component/Modal/AddBlogsModal";
import AddSitesModal from "./Component/Modal/AddSitesModal";

// import MyComponent from "./Component/myComponent";
function About() {
  const [message, setmessage] = useState([]);
  const [properties, setproperties] = useState([]);
  const [blogs, setblogs] = useState([]);
  const [Enquiry, setEnquiry] = useState([]);
  const [loader, setloader] = useState(false);
  const [AddSites, setAddSites] = useState(false);
  const [Addblogs, setAddblogs] = useState(false);
  const [viewmessage, setviewmessage] = useState(false);

  const [page, setpage] = React.useState(0);
  const [MessageTable, setMessageTable] = useState(false);
  const [PropertyTable, setPropertyTable] = useState(false);
  const [BlogsTable, setBlogsTable] = useState(false);
  const [Enquirytable, setEnquirytable] = useState(false);
  const [rowsperpage, setrowsperpage] = React.useState(5);
  // const Token = sessionStorage.getItem("Token");
  const [cookies, setCookie] = useCookies(["cookie"]);

  const history = useHistory();
  //   const router = useRouter();
  const openProperty = () => {
    setPropertyTable(true);
    setMessageTable(false);
    setBlogsTable(false);
    setEnquirytable(false);
  };
  const openEnquiry = () => {
    setPropertyTable(false);
    setMessageTable(false);
    setBlogsTable(false);
    setEnquirytable(true);
  };

  const openBlogs = () => {
    setPropertyTable(false);
    setMessageTable(false);
    setBlogsTable(true);
    setEnquirytable(false);
  };
  const openMessage = () => {
    setPropertyTable(false);
    setMessageTable(true);
    setBlogsTable(false);
    setEnquirytable(false);
  };
  const onChangePage = (e, newPage) => {
    setpage(newPage);
  };

  const onChangeRowsPerPage = (e) => {
    setrowsperpage(e.target.value);
  };
  const HandelDeleteProperty = async (id) => {
    setloader(true);
    const res = await fetch("api/DeleteProperty", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ id }),
    });
    const data = await res.json();
    if (data) {
      PropertyData();
    }
  };

  const AdminCheck = (id) => {
    // setloader(true);
    // const res = await fetch("api/AdminCheck", {
    //   method: "POST",
    //   headers: {
    //     "Content-Type": "application/json",
    //   },
    //   body: JSON.stringify({ Token }),
    // });
    // const data = await res.json();
    // console.log(data);

    // cookies &&

    const check = cookies.cookie === "qwertyasdfgzxcvb1237654";
    if (!check) {
      history.push("/login");
    }
  };
  const HandelDeleteBlogs = async (id) => {
    setloader(true);

    const res = await fetch("api/DeleteBlog", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ id }),
    });
    const data = await res.json();
    if (data) {
      BlogsData();
    }
  };

  const MessageData = async () => {
    setloader(true);

    try {
      const res = await fetch("api/adminMessage", {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      const data = await res.json();

      if (!res.status === 200) {
        const error = new Error("invaldated..........");
        throw error;
      }
      setmessage(data);
      setloader(false);

      //   setval(true);
    } catch (err) {
      setloader(false);

      history.push("/login");
    }
  };

  const PropertyData = async () => {
    setloader(true);

    try {
      const res = await fetch(
        "api/GetPropertyAdmin",
        {
          method: "GET",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
        }
      );
      const data = await res.json();

      if (!res.status === 200) {
        const error = new Error("invaldated..........");
        throw error;
      }
      setproperties(data);
      setloader(false);

      //   setval(true);
    } catch (err) {
      setloader(false);

      history.push("/login");
    }
  };
  const BlogsData = async () => {
    setloader(true);

    try {
      const res = await fetch("api/AdminBlogs", {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      const data = await res.json();

      if (!res.status === 200) {
        const error = new Error("invaldated..........");
        throw error;
      }
      setblogs(data);
      setloader(false);

      //   setval(true);
    } catch (err) {
      setloader(false);

      history.push("/login");
    }
  };
  const EnquiryData = async () => {
    setloader(true);

    try {
      const res = await fetch("api/GetEnquiry", {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      const data = await res.json();

      if (!res.status === 200) {
        const error = new Error("invaldated..........");
        throw error;
      }
      setEnquiry(data);
      setloader(false);

      //   setval(true);
    } catch (err) {
      setloader(false);

      //   router.push("/login");
    }
  };

  useEffect(() => {
    AdminCheck();
    MessageData();
    PropertyData();
    BlogsData();
    EnquiryData();

    setMessageTable(true);
    setPropertyTable(false);
    setBlogsTable(false);
    setEnquirytable(false);
  }, []);
  return (
    <div className="pt-4">
      <div className="py-5">
        {/* <Fab color="primary" aria-label="add">
          <AddIcon />
        </Fab>
        <Button
          variant="contained"
          color="primary"
          onClick={() => setAddSites(true)}
        >
          Add property
        </Button>
        <Button
          variant="contained"
          color="primary"
          onClick={() => setAddblogs(true)}
        >
          Add Blog
        </Button> */}
        <AddSitesModal
          PropertyData={PropertyData}
          modal={AddSites}
          setModal={setAddSites}
        />
        <AddBlogsModal
          BlogsData={BlogsData}
          modal={Addblogs}
          setModal={setAddblogs}
        />
      </div>
      <div className="container-fluid">
        <div className="row">
          <div className="col-3">
            <Paper elevation={5}>
              {/* <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  openMessage();
                }}
              >
                Messages
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  openEnquiry();
                }}
              >
                Enquiry
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  openProperty();
                }}
              >
                Property
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  openBlogs();
                }}
              >
                Blogs
              </Button> */}
              <List disablePadding>
                <ListItem
                  component={Button}
                  // color={MessageTable && "primary"}
                  variant={MessageTable && "contained"}
                  onClick={() => {
                    openMessage();
                  }}
                >
                  <ListItemIcon>{/* <HomeIcon /> */}</ListItemIcon>
                  {/* <Link href="/"> */}
                  <ListItemText primary="  Messages" />
                  {/* </Link> */}
                </ListItem>
                <Divider />
                <ListItem
                  component={Button}
                  // color={MessageTable && "primary"}
                  variant={Enquirytable && "contained"}
                  onClick={() => {
                    openEnquiry();
                  }}
                >
                  <ListItemIcon>{/* <InfoIcon /> */}</ListItemIcon>
                  {/* <Link href="/About"> */}
                  <ListItemText primary="Enquiry" />
                  {/* </Link> */}
                </ListItem>
                <Divider />
                <ListItem
                  component={Button}
                  // color={MessageTable && "primary"}
                  variant={PropertyTable && "contained"}
                  onClick={() => {
                    openProperty();
                  }}
                >
                  <ListItemIcon>{/* <InfoIcon /> */}</ListItemIcon>
                  {/* <Link href="/About"> */}
                  <ListItemText primary="Property" />
                  {/* </Link> */}
                </ListItem>
                <Divider />
                <ListItem
                  component={Button}
                  // color={MessageTable && "primary"}
                  variant={BlogsTable && "contained"}
                  onClick={() => {
                    openBlogs();
                  }}
                >
                  <ListItemIcon>{/* <InfoIcon /> */}</ListItemIcon>
                  {/* <Link href="/About"> */}
                  <ListItemText primary="Blogs" />
                  {/* </Link> */}
                </ListItem>
              </List>
            </Paper>
          </div>
          <div className="col-9">
            <div>
              {MessageTable && (
                <TableContainer component={Paper} elevation={5}>
                  <Table>
                    <TableHead>
                      <TableRow>
                        {/* <TableCell>No.</TableCell> */}

                        <TableCell>Name</TableCell>
                        <TableCell>Email</TableCell>
                        <TableCell>Phone</TableCell>
                        <TableCell>Message</TableCell>
                        <TableCell>Actions</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {message?.rootUser
                        ?.slice(
                          page * rowsperpage,
                          page * rowsperpage + rowsperpage
                        )
                        ?.map((res, id) => (
                          <TableRow key={id}>
                            {/* <TableCell>{id + 1}</TableCell> */}
                            <TableCell>{res.name}</TableCell>
                            <TableCell>{res.email}</TableCell>
                            <TableCell>{res.phone}</TableCell>
                            <TableCell>
                              {res.message.substr(0, 50)}...
                            </TableCell>
                            <TableCell>
                              <VisibilityIcon
                                onClick={() => {
                                  history.push(`/message/${res._id}`);
                                }}
                              />
                            </TableCell>
                          </TableRow>
                        ))}
                    </TableBody>
                  </Table>
                  <TablePagination
                    rowsPerPageOptions={[5, 10, 15, 20, 25, 30]}
                    count={message?.rootUser?.length}
                    rowsPerPage={rowsperpage}
                    page={page}
                    onChangePage={onChangePage}
                    onChangeRowsPerPage={onChangeRowsPerPage}
                  />
                </TableContainer>
              )}
              {Enquirytable && (
                <TableContainer component={Paper} elevation={5}>
                  <Table>
                    <TableHead>
                      <TableRow>
                        {/* <TableCell>No.</TableCell> */}

                        <TableCell>Name</TableCell>
                        <TableCell>Email</TableCell>
                        <TableCell>Phone</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {Enquiry?.rootUser
                        ?.slice(
                          page * rowsperpage,
                          page * rowsperpage + rowsperpage
                        )
                        ?.map((res, id) => (
                          <TableRow key={id}>
                            {/* <TableCell>{id + 1}</TableCell> */}
                            <TableCell>{res.name}</TableCell>
                            <TableCell>{res.email}</TableCell>
                            <TableCell>{res.number}</TableCell>
                          </TableRow>
                        ))}
                    </TableBody>
                  </Table>
                  <TablePagination
                    rowsPerPageOptions={[5, 10, 15, 20, 25, 30]}
                    count={Enquiry?.rootUser?.length}
                    rowsPerPage={rowsperpage}
                    page={page}
                    onChangePage={onChangePage}
                    onChangeRowsPerPage={onChangeRowsPerPage}
                  />
                </TableContainer>
              )}
              {PropertyTable && (
                <TableContainer component={Paper} elevation={5}>
                  <Table>
                    <TableHead>
                      <TableRow>
                        {/* <TableCell>No.</TableCell> */}

                        <TableCell>Name of site</TableCell>
                        <TableCell>price</TableCell>
                        <TableCell>Images</TableCell>
                        <TableCell>Address</TableCell>
                        <TableCell>Actions</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {properties?.rootUser
                        ?.slice(
                          page * rowsperpage,
                          page * rowsperpage + rowsperpage
                        )
                        ?.map((res, id) => (
                          <TableRow key={id}>
                            {/* <TableCell>{id + 1}</TableCell> */}
                            <TableCell>{res.name}</TableCell>
                            <TableCell>{res.price}</TableCell>
                            <TableCell>{res.images}</TableCell>
                            <TableCell>
                              {res.address.substr(0, 50)}...
                            </TableCell>
                            <TableCell>
                              <VisibilityIcon
                                onClick={() => {
                                  history.push(`/property/${res._id}`);
                                }}
                              />
                              <DeleteIcon
                                onClick={() => HandelDeleteProperty(res._id)}
                              />
                            </TableCell>
                          </TableRow>
                        ))}
                    </TableBody>
                  </Table>
                  <TablePagination
                    rowsPerPageOptions={[5, 10, 15, 20, 25, 30]}
                    count={properties?.rootUser?.length}
                    rowsPerPage={rowsperpage}
                    page={page}
                    onChangePage={onChangePage}
                    onChangeRowsPerPage={onChangeRowsPerPage}
                  />
                </TableContainer>
              )}
              {BlogsTable && (
                <TableContainer component={Paper} elevation={5}>
                  <Table>
                    <TableHead>
                      <TableRow>
                        {/* <TableCell>No.</TableCell> */}

                        <TableCell>Heading</TableCell>
                        <TableCell>Image</TableCell>
                        <TableCell>Description</TableCell>
                        <TableCell>Actions</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {blogs?.rootUser
                        ?.slice(
                          page * rowsperpage,
                          page * rowsperpage + rowsperpage
                        )
                        ?.map((res, id) => (
                          <TableRow key={id}>
                            {/* <TableCell>{id + 1}</TableCell> */}
                            <TableCell>{res.name}</TableCell>
                            <TableCell>{res.images}</TableCell>
                            <TableCell>
                              {res.description.substr(0, 50)}...
                            </TableCell>
                            <TableCell>
                              <VisibilityIcon
                                onClick={() => {
                                  history.push(`/blog/${res._id}`);
                                }}
                              />
                              <DeleteIcon
                                onClick={() => HandelDeleteBlogs(res._id)}
                              />
                            </TableCell>
                          </TableRow>
                        ))}
                    </TableBody>
                  </Table>
                  <TablePagination
                    rowsPerPageOptions={[5, 10, 15, 20, 25, 30]}
                    count={blogs?.rootUser?.length}
                    rowsPerPage={rowsperpage}
                    page={page}
                    onChangePage={onChangePage}
                    onChangeRowsPerPage={onChangeRowsPerPage}
                  />
                </TableContainer>
              )}
            </div>
          </div>
        </div>
      </div>
      <div>
        {/* <MyComponent showh2={true} /> */}
        <FloatingIcons setAddSites={setAddSites} setAddblogs={setAddblogs} />
      </div>
      <Loader val={loader} />
    </div>
  );
}

export default About;
