import React, { useEffect } from "react";
import { useCookies } from "react-cookie";

import { useHistory } from "react-router";
function Logout() {
  const history = useHistory();
  const [cookies, setCookie] = useCookies(["cookie"]);

  useEffect(() => {
    const LogOut = () => {
      setCookie("cookie", "");

      history.push("/");
    };
    LogOut();
  }, []);
  return <div></div>;
}

export default Logout;
