import { Button, Paper } from "@material-ui/core";
import Modal from "@material-ui/core/Modal";
import { makeStyles } from "@material-ui/core/styles";
import React from "react";
import { storage } from "../Firebase";

function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50 + rand();
  const left = 50 + rand();

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

export default function SimpleModal({ PropertyData, modal, setModal }) {
  const classes = useStyles();
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = React.useState(getModalStyle);
  const [Data, setData] = React.useState({
    name: "",
    price: "",
    unit: "",
    images: "",
    address: "",
    Beds: "",
    Baths: "",
    sqft: "",
  });
  const [tag, settag] = React.useState("");
  const [tags, settags] = React.useState([]);
  const [images, setimages] = React.useState("");
  const [progress, setprogress] = React.useState(0);
  const [pdfs, setpdfs] = React.useState("");
  const [progresspdf, setprogresspdf] = React.useState(0);

  const [onWards, setonWards] = React.useState(false);
  const handleClickCheck = () => setonWards(!onWards);
  const handleClose = () => {
    setModal(false);
  };
  console.log(Data);
  const changeHandler = (e) => {
    let name = e.target.name;
    let value = e.target.value;
    setData({ ...Data, [name]: value });
  };
  const handelSubmit = async (e) => {
    e.preventDefault();
    const { name, price, address, Beds, Baths, sqft, unit } = Data;
    const res = await fetch("api/postProperty", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name,
        price,
        unit,
        images,
        address,
        Beds,
        Baths,
        sqft,
        onWards,
        tags,
        pdfs,
      }),
    });
    const data = await res.json();

    console.log(data);
    if (data.message) {
      window.alert("success fulll");
      PropertyData();
      setModal(false);
      // setval(true);
    } else {
      window.alert(data.error);

      // dispatch({ type: "USER", payload: true });
    }
    setData({
      name: "",
      price: "",
      unit: "",
      images: "",
      address: "",
      Beds: "",
      Baths: "",
      sqft: "",
    });
    settags([]);
    setonWards(false);
  };
  const setTag = () => {
    settag("");
    settags([...tags, tag]);
  };
  const handleChageImage = (e) => {
    e.preventDefault();
    if (e.target.files[0]) {
      const image = e.target.files[0];
      const uploadTask = storage.ref(`images/${image.name}`).put(image);
      uploadTask.on(
        "state_changed",
        (snapShot) => {
          //takes a snap shot of the process as it is happening
          // console.log(snapShot);
          const pro = Math.round(
            (snapShot.bytesTransferred / snapShot.totalBytes) * 100
          );
          setprogress(pro);
        },
        (err) => {
          //catches the errors
          console.log(err);
        },
        () => {
          // gets the functions from storage refences the image storage in firebase by the children
          // gets the download url then sets the image from firebase as the value for the imgUrl key:
          storage
            .ref("images")
            .child(image.name)
            .getDownloadURL()
            .then((fireBaseUrl) => {
              setimages(fireBaseUrl);
            });
        }
      );
    }
  };
  const handleChagePdf = (e) => {
    e.preventDefault();
    if (e.target.files[0]) {
      const pdf = e.target.files[0];
      const uploadTask = storage.ref(`PDF/${pdf.name}`).put(pdf);
      uploadTask.on(
        "state_changed",
        (snapShot) => {
          //takes a snap shot of the process as it is happening
          // console.log(snapShot);
          const pro = Math.round(
            (snapShot.bytesTransferred / snapShot.totalBytes) * 100
          );
          setprogresspdf(pro);
        },
        (err) => {
          //catches the errors
          console.log(err);
        },
        () => {
          // gets the functions from storage refences the image storage in firebase by the children
          // gets the download url then sets the image from firebase as the value for the imgUrl key:
          storage
            .ref("PDF")
            .child(pdf.name)
            .getDownloadURL()
            .then((fireBaseUrl) => {
              setpdfs(fireBaseUrl);
            });
        }
      );
    }
  };

  const body = (
    <div style={modalStyle} className={classes.paper}>
      <div>
        <form onSubmit={handelSubmit}>
          <input name="name" placeholder="name" onChange={changeHandler} />
          <input name="price" placeholder="price" onChange={changeHandler} />
          <input
            name="unit"
            placeholder="thousands,lakhs,crores"
            onChange={changeHandler}
          />
          <div class="form-check">
            <input
              class="form-check-input"
              type="checkbox"
              checked={onWards}
              id="flexCheckDefault"
              onClick={handleClickCheck}
            />
            {/* <input
              onClick={handleClickCheck}
              checked={onWards}
              type="checkbox"
            /> */}
            <label class="form-check-label" for="flexCheckDefault">
              onWards
            </label>
          </div>
          {/* <input name="images" placeholder="images" onChange={changeHandler} />
           */}
          <p>Image</p>
          <input type="file" onChange={handleChageImage} />
          <progress max="100" value={progress} />
          <p>upload pdf</p>
          <input type="file" onChange={handleChagePdf} />
          <progress max="100" value={progresspdf} />
          <input
            name="address"
            placeholder="address"
            onChange={changeHandler}
          />
          <input name="Beds" placeholder="Beds" onChange={changeHandler} />
          <input name="Baths" placeholder="Baths" onChange={changeHandler} />
          <input name="sqft" placeholder="sqft" onChange={changeHandler} />
          <input
            name="tag"
            placeholder="tags"
            value={tag}
            onChange={(e) => settag(e.target.value)}
          />
          <Button variant="contained" color="primary" onClick={() => setTag()}>
            Add Tag
          </Button>
          <Button type="submit" variant="contained" color="primary">
            Submit
          </Button>
        </form>
      </div>

      <SimpleModal />
    </div>
  );

  return (
    <div>
      <Modal
        component={Paper}
        open={modal}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </div>
  );
}
