import {
  Button,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Divider,
  Fab,
} from "@material-ui/core";
import SupervisorAccountIcon from "@material-ui/icons/SupervisorAccount";
import React, { useEffect, useRef } from "react";
import HomeIcon from "@material-ui/icons/Home";
import AnnouncementIcon from "@material-ui/icons/Announcement";
import BookIcon from "@material-ui/icons/Book";
import InfoIcon from "@material-ui/icons/Info";
import LocalAtmIcon from "@material-ui/icons/LocalAtm";
import MoreVertIcon from "@material-ui/icons/MoreVert";
import PermContactCalendarIcon from "@material-ui/icons/PermContactCalendar";
import { Link, useLocation } from "react-router-dom";
import logo from "../Assests/blackstone_logo.png";
import LockIcon from "@material-ui/icons/Lock";

function Header() {
  const [drawer, setdrawer] = React.useState(false);
  const [navBackground, setNavBackground] = React.useState(false);
  const navRef = useRef();
  const router = useLocation();
  navRef.current = navBackground;
  useEffect(() => {
    const handleScroll = () => {
      const show = window.scrollY > 50;
      if (navRef.current !== show) {
        setNavBackground(show);
      }
    };
    document.addEventListener("scroll", handleScroll);
    return () => {
      document.removeEventListener("scroll", handleScroll);
    };
  }, []);
  return (
    <div className="main">
      <div className="Check">
        <nav
          className={`
          ${
            navBackground
              ? "navbar fixed-top navbar-expand-lg navbar-dark bg-dark  py-sm-4 px-4"
              : "navbar fixed-top navbar-expand-lg navbar-dark py-sm-4 px-4"
          }  `}
        >
          <div className="container-fluid">
            <Link to="/" className="navbar-brand">
              <img
                src={logo}
                className="header__image d-inline-block align-text-top"
              />
            </Link>

            <IconButton
              className="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarNav"
              aria-controls="navbarNav"
              aria-expanded="false"
              aria-label="Toggle navigation"
              // size="small"
              color="primary"
              // variant="contained"

              onClick={() => setdrawer(true)}
            >
              <MoreVertIcon style={{ color: "blue" }} />
            </IconButton>

            <div
              className=" collapse navbar-collapse"
              id="navbarSupportedContent"
            >
              <ul className="navbar-nav  ms-auto mb-2 mb-lg-0">
                <>
                  <li className="nav-item px-3">
                    <Link to="/">
                      <Button
                        variant={
                          router.pathname === "/" ? "contained" : "outlined"
                        }
                        color="primary"
                      >
                        Home
                      </Button>
                    </Link>
                  </li>
                  <li className="nav-item px-3">
                    <Link to="/About">
                      <Button
                        variant={
                          router.pathname === "/About"
                            ? "contained"
                            : "outlined"
                        }
                        color="primary"
                      >
                        About Us
                      </Button>
                    </Link>
                  </li>

                  <li className="nav-item px-3">
                    <Link to="/Blogs">
                      <Button
                        variant={
                          router.pathname === "/Blogs"
                            ? "contained"
                            : "outlined"
                        }
                        color="primary"
                      >
                        Blog
                      </Button>
                    </Link>
                  </li>

                  {/* <li className="nav-item px-3">
                    <Link to="/News">
                      <Button
                        variant={
                          router.route === "/News" ? "contained" : "outlined"
                        }
                        color="primary"
                      >
                        News
                      </Button>
                    </Link>
                  </li> */}
                  <li className="nav-item px-3">
                    <Link to="/EmiCalculator">
                      <Button
                        variant={
                          router.pathname === "/EmiCalculator"
                            ? "contained"
                            : "outlined"
                        }
                        color="primary"
                      >
                        Emi Calculator
                      </Button>
                    </Link>
                  </li>
                  <li className="nav-item px-3">
                    <Link to="/Contact">
                      <Button
                        variant={
                          router.pathname === "/Contact"
                            ? "contained"
                            : "outlined"
                        }
                        color="primary"
                      >
                        Contact
                      </Button>
                    </Link>
                  </li>
                  <li className="nav-item px-3">
                    {router.pathname !== "/Admin" && (
                      <Link to="/Admin">
                        <Button
                          variant={
                            router.pathname === "/Admin"
                              ? "contained"
                              : "outlined"
                          }
                          color="primary"
                        >
                          <SupervisorAccountIcon />
                        </Button>
                      </Link>
                    )}
                    {router.pathname === "/Admin" && (
                      <Link to="/logout">
                        <Button
                          variant={
                            router.pathname === "/Admin"
                              ? "contained"
                              : "outlined"
                          }
                          color="primary"
                        >
                          <LockIcon />
                        </Button>
                      </Link>
                    )}
                  </li>
                </>
              </ul>
            </div>
          </div>
        </nav>
      </div>
      <Drawer open={drawer} onClose={() => setdrawer(false)} anchor="right">
        <List disablePadding>
          <Link style={{ textDecoration: "none" }} to="/">
            <ListItem button>
              <ListItemIcon>
                <HomeIcon />
              </ListItemIcon>
              <ListItemText primary="HOME" onClick={() => setdrawer(false)} />
            </ListItem>
          </Link>

          <Divider />
          <Link style={{ textDecoration: "none" }} to="/About">
            <ListItem button>
              <ListItemIcon>
                <InfoIcon />
              </ListItemIcon>
              <ListItemText
                primary="ABOUT US"
                onClick={() => setdrawer(false)}
              />
            </ListItem>
          </Link>

          <Divider />
          <Link style={{ textDecoration: "none" }} to="/Blogs">
            <ListItem button>
              <ListItemIcon>
                <BookIcon />
              </ListItemIcon>
              <ListItemText primary="BLOG" onClick={() => setdrawer(false)} />
            </ListItem>
          </Link>

          <Divider />
          <Link style={{ textDecoration: "none" }} to="/EmiCalculator">
            <ListItem button>
              <ListItemIcon>
                <LocalAtmIcon />
              </ListItemIcon>
              <ListItemText
                primary="EMI CALCULATOR"
                onClick={() => setdrawer(false)}
              />
            </ListItem>
          </Link>

          <Divider />
          <Link style={{ textDecoration: "none" }} to="/Contact">
            <ListItem button>
              <ListItemIcon>
                <PermContactCalendarIcon />
              </ListItemIcon>
              <ListItemText
                primary="CONTACT"
                onClick={() => setdrawer(false)}
              />
            </ListItem>
          </Link>

          <Divider />
          {/* <ListItem button>
            <ListItemIcon>
              <AnnouncementIcon />
            </ListItemIcon>
            <Link style={{ textDecoration: "none" }} to="/Admin">
              <ListItemText primary="Admin" />
            </Link>
          </ListItem> */}
        </List>
      </Drawer>
    </div>
  );
}

export default Header;
