import React, { useState } from "react";
import { useHistory } from "react-router";
import Footer from "./Component/Footer";
import logo from "./undraw_secure_login_pdn4.svg";
import Loader from "./Component/loader";
function Signup() {
  const [Signup, setSignup] = useState({
    name: "",
    email: "",
    security: "",

    password: "",
    Cpassword: "",
  });
  const [loader, setloader] = useState(false);
  const history = useHistory();
  const ChangeHandler = (e) => {
    var name = e.target.name;
    var value = e.target.value;
    setSignup({ ...Signup, [name]: value });
  };
  console.log(Signup);
  const handelSubmit = async (e) => {
    e.preventDefault();
    setloader(true);
    const { name, email, security, password, Cpassword } = Signup;
    const res = await fetch("api/register", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name,
        email,
        security,
        password,
        Cpassword,
      }),
    });
    const data = await res.json();
    if (data && data.message) {
      window.alert("succesfull");
      history.push("/login");
      setloader(false);
    } else {
      console.log(data);
      setloader(false);

      window.alert(data.error);
    }
    // if (data.status === 422 || !data) {
    //   window.alert("invalid");
    // } else {
    //   window.alert("succesfull");
    //   history.push("/login");
    // }
  };
  return (
    <div>
      <div className="container pt-5 mt-5">
        <div className="row">
          <div className="col-10 mx-auto mt-5 main_div">
            <div className="row">
              <div className="col-6 ">
                <h2 className="text-center mt-4 mb-5">signup</h2>
                <div className="row">
                  <div className="col-8 m-auto">
                    <form method="POST" onSubmit={handelSubmit}>
                      <div className=" mb-3 form-group">
                        <input
                          type="text"
                          name="name"
                          onChange={ChangeHandler}
                          className="form-control input-sm"
                          placeholder="Your Name..."
                          value={Signup.name}
                        />
                      </div>
                      <div className="mb-3">
                        <input
                          type="email"
                          name="email"
                          onChange={ChangeHandler}
                          className="form-control"
                          placeholder="Your Email..."
                          value={Signup.email}
                        />
                      </div>
                      <div className="mb-3">
                        <input
                          name="security"
                          onChange={ChangeHandler}
                          type="number"
                          className="form-control"
                          value={Signup.security}
                          placeholder="Your security number...."
                        />
                      </div>

                      <div className="mb-3">
                        <input
                          name="password"
                          onChange={ChangeHandler}
                          type="password"
                          value={Signup.password}
                          className="form-control"
                          placeholder="password..."
                        />
                      </div>
                      <div className="mb-3">
                        <input
                          name="Cpassword"
                          onChange={ChangeHandler}
                          value={Signup.Cpassword}
                          type="password"
                          className="form-control"
                          placeholder="confirm password..."
                        />
                      </div>

                      <button type="submit" className="btn btn-primary my-4">
                        Submit
                      </button>
                    </form>
                  </div>
                </div>
              </div>
              <div className="col-6 m-auto">
                <img className="img" src={logo} alt="asd" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div style={{ marginTop: "280px" }}>
        <Footer />
        <Loader val={loader} />
      </div>
    </div>
  );
}

export default Signup;
