import { Paper } from "@material-ui/core";
import React from "react";
import { useHistory, useParams } from "react-router-dom";
function Messages() {
  const [message, setmessage] = React.useState(null);
  let { id } = useParams();
  const history = useHistory();
  React.useEffect(() => {
    const CallMessage = async (id) => {
      const res = await fetch(
        "api/adminMessageSingle",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ id }),
        }
      );
      const data = await res.json();
      if (data.rootUser.length > 0) {
        setmessage(data.rootUser);
        console.log(data);
      } else {
        window.alert("please login first");
        history.push("/login");
      }
    };
    CallMessage(id);
  }, []);
  return (
    <div className="container pt-5 mt-5">
      <div className="row">
        <div className="col-6 m-auto pt-5 ">
          {message && (
            <Paper elevation={5} className="p-4">
              <h2 className="text-center">Message</h2>
              <p className="text-center">
                <strong>name:-</strong>
                {message[0]?.name}
              </p>
              <p className="text-center">
                <strong>phone:-</strong>
                {message[0]?.phone}
              </p>
              <p className="text-center">
                <strong>email:-</strong>

                <a href={`mailto:${message[0]?.email}`}>{message[0]?.email}</a>
              </p>
              <p>
                <strong>message:-</strong>
              </p>
              <p> {message[0]?.message}</p>
            </Paper>
          )}
        </div>
      </div>
    </div>
  );
}

export default Messages;
