import {
  Button,
  Paper,
  Slider,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TablePagination,
  TableRow,
  Typography,
} from "@material-ui/core";
import Loanjs from "loanjs";
import React from "react";
import { Doughnut } from "react-chartjs-2";
import EmiSvg from "./Assests/Images/Emi.svg";
import Footer from "./Component/Footer";
// import Image from "next/image";
import "./Emi.css";
function EmiCalculator() {
  const [amount, setamount] = React.useState();
  const [months, setmonths] = React.useState(12);
  const [CalculatedData, setCalculatedData] = React.useState();
  const [grapharr, setgraphar] = React.useState([]);
  const [page, setpage] = React.useState(0);
  const [rowsperpage, setrowsperpage] = React.useState(5);

  // React.useEffect(() => {
  //   console.log(window.innerHeight);
  // }, [window.innerHeight]);
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  console.log(CalculatedData);

  const [intrest, setintrest] = React.useState();
  const onChangePage = (e, newPage) => {
    setpage(newPage);
  };
  const onChangeRowsPerPage = (e) => {
    setrowsperpage(e.target.value);
  };
  function valuetext(value) {
    const years = value * 12;
    setmonths(years);
  }
  function Intrest(value) {
    setintrest(value);
  }
  const Loancalculate = (e) => {
    e.preventDefault();
    let Loan = Loanjs.Loan(amount, months, intrest);
    setCalculatedData(Loan);
  };
  React.useEffect(() => {
    const Valu = [];
    Valu.push(CalculatedData?.amount, CalculatedData?.interestSum);
    setgraphar(Valu);
  }, [CalculatedData]);
  const options = {
    legend: {
      display: false,
      position: "right",
    },
    elements: {
      arc: {
        borderWidth: 0,
      },
    },
  };

  const data = {
    maintainAspectRatio: false,
    responsive: false,
    labels: ["Amount", "Interest Amount"],
    datasets: [
      {
        data: grapharr,
        backgroundColor: [
          "rgb(255, 99, 132)",
          "rgb(54, 162, 235)",
          "rgb(255, 205, 86)",
        ],
        hoverOffset: 4,
      },
    ],
  };

  return (
    <div className="MainDiv">
      <div className="container ">
        <div className="MainCont">
          <div className="row ">
            <div className="col-md-6 py-5 ">
              <div className="LeftDiv">
                <div class="card shadow rounded">
                  <div class="card-body">
                    <h5 class="card-title">Emi Calculator</h5>
                    <form onSubmit={(e) => Loancalculate(e)}>
                      <div className="row">
                        <div className="py-2">
                          {/* <TextField
                            id="standard-full-width"
                            label="Amount"
                            style={{ margin: "auto", width: "auto" }}
                            placeholder="Enter The Amount!"
                            fullWidth
                            margin="normal"
                            onChange={(e) => setamount(e.target.value)}
                            InputProps={
                              {
                                // startAdornment: (
                                //   <InputAdornment position="start">
                                //     <AccountCircle />
                                //   </InputAdornment>
                                // ),
                                // shrink: true,
                              }
                            }
                            variant="outlined"
                          /> */}
                          <input
                            placeholder="Amount"
                            onChange={(e) => setamount(e.target.value)}
                            class="form-control"
                          />
                        </div>

                        <div className="py-2">
                          {" "}
                          <Typography gutterBottom>TENURE (Years)</Typography>
                          <Slider
                            defaultValue={12}
                            getAriaValueText={valuetext}
                            aria-labelledby="discrete-slider-small-steps"
                            step={1}
                            marks
                            min={1}
                            max={30}
                            valueLabelDisplay="auto"
                          />
                        </div>
                        <div className="py-2">
                          <Typography gutterBottom>
                            INTEREST RATE (% P.A.)
                          </Typography>
                          <Slider
                            defaultValue={0.05}
                            getAriaValueText={Intrest}
                            aria-labelledby="discrete-slider-small-steps"
                            step={0.01}
                            marks
                            min={0}
                            max={15}
                            valueLabelDisplay="auto"
                          />
                        </div>
                      </div>

                      <Button variant="contained" type="submit" color="primary">
                        Calculate
                      </Button>
                    </form>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-6 d-none d-md-block d-lg-block d-xl-block mt-4">
              <img src={EmiSvg} style={{ maxWidth: "100%" }} alt="imag" />
            </div>
          </div>
          {CalculatedData && (
            <div className="row">
              <div className="col-md-12 col-lg-6 py-5">
                <div className="row ">
                  <div className="col-md-6 col-sm-12">
                    <Doughnut data={data} options={options} />
                  </div>
                  <div className="col-md-6 col-sm-12  ">
                    <div className="d-flex justify-content-center align-items-center pt-3">
                      <Paper elevation={3}>
                        <div className="p-3">
                          <h4>Amount:{Math.ceil(CalculatedData.amount)}</h4>
                          <h4>rate of Interest:{intrest}</h4>
                          <h4>Duration:{months / 12} years</h4>

                          <h4>
                            Interest Amount:
                            {Math.ceil(CalculatedData.interestSum)}
                          </h4>
                          <h4>Total Amount:{Math.ceil(CalculatedData.sum)}</h4>
                        </div>
                      </Paper>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-md-12 col-lg-6">
                <TableContainer component={Paper} elevation={5}>
                  <Table>
                    <TableHead>
                      <TableRow>
                        {/* <TableCell>No.</TableCell> */}

                        <TableCell>Capital</TableCell>
                        <TableCell>Installment</TableCell>
                        <TableCell>Interest</TableCell>
                        <TableCell>InterestSum</TableCell>
                        <TableCell>Remain</TableCell>
                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {CalculatedData?.installments
                        ?.slice(
                          page * rowsperpage,
                          page * rowsperpage + rowsperpage
                        )
                        ?.map((res, id) => (
                          <TableRow>
                            {/* <TableCell>{id + 1}</TableCell> */}
                            <TableCell>{Math.ceil(res.capital)}</TableCell>
                            <TableCell>{Math.ceil(res.installment)}</TableCell>
                            <TableCell>{res.interest}</TableCell>
                            <TableCell>{Math.ceil(res.interestSum)}</TableCell>
                            <TableCell>{Math.ceil(res.remain)}</TableCell>
                          </TableRow>
                        ))}
                    </TableBody>
                  </Table>
                  <TablePagination
                    rowsPerPageOptions={[5, 10, 15, 20, 25, 30]}
                    count={CalculatedData?.installments.length}
                    rowsPerPage={rowsperpage}
                    page={page}
                    onChangePage={onChangePage}
                    onChangeRowsPerPage={onChangeRowsPerPage}
                  />
                </TableContainer>
              </div>
            </div>
          )}
        </div>
      </div>
      {!CalculatedData && <div className="pt-5 mt-5"></div>}
      <Footer />
    </div>
  );
}

export default EmiCalculator;
